}

.Common-title-medium {
  font-size: 2em;
  text-align: center;
}

.Blink-fb-comments {
  width: 70%;
}

@media screen and (max-width: 60em) {
  .Blink-fb-comments {
    width: 90%;
  }
}
.Header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 60px;
    display: flex;
    flex-direction: row;
    background: transparent;
    z-index: 2;
}

.Logo-container {
    display: flex;
    align-items: center;
    justify-content: left;
    margin-left: 10px;
    width: 50%;
    height: 100%;
}

.Logo {
    display: block;
    width: auto;
    height: 90%;
    cursor: pointer;
}

.Header-button {
    font-size: 1em;
    padding-right: 1em;
    padding-left: 1em;
    cursor: pointer;
}

.Header-button-container {
    width: 50%;
    height: 100%;
    margin-right: 1em;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.Header-mobile-button-container {
    width: 50%;
    height: 100%;
    margin-right: 1em;
    display: none;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

@media screen and (max-width: 60em) {
    .Header-button-container {
        display: none;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
    }
    
    .Header-mobile-button-container {
        display: flex;
    }
}


.Background {
    background: #e5e5e5
}

.Background-x {
    background: #f1f1f1
}

.Blink {
    color: #f55d5d
}

.Blink-border {
    border-color: #f55d5d;
}

.Blink-outline {
    outline-color: #f55d5d;
}

.Blink-background {
    background-color: #f55d5d;
}

.Blink-paragraph {
    color: dimgray;
}

.Blink-code-background {
    background-color: #F6F8FA
}
/* Header Button */

.Header-button {
    font-size: 1em;
    padding: 0.5em 1em;
    cursor: pointer;
}

.Header-button-container .Header-dropdown-content {
    display: none;
    color: white;
    position: absolute;
    width: 200%;
    right: 0;
}

.Header-mobile-button-container .Header-dropdown-content {
    display: none;
    width: 100%;
}

.Header-mobile-button-container .Header-dropdown {
    width: 100%;
    text-align: center;
}

.Header-mobile-button-container .Header-dropdown div {
    text-align: center;
}

.Header-dropdown-content div {
    padding: 0.5em 1em;
    text-align: left;
}

.Header-dropdown-content div:hover {
    background-color: #f44e4e;
}

.Header-dropdown {
    position: relative;
}


.Header-dropdown:hover .Header-dropdown-content {
    display: block;
}

/* Header Button End */

/* Ham */

.Header-ham-container {
    display: inline-block;
    cursor: pointer;
    font-size: 0.8em;
}

.Header-ham-bar1, .Header-ham-bar2, .Header-ham-bar3 {
    width: 2.5em;
    height: 0.3em;
    margin: 0.4em 0;
    transition: 0.4s;
}


.Ham-change .Header-ham-bar1 {
    -webkit-transform: rotate(-45deg) translate(-0.6em,0.4em);
    transform: rotate(-45deg) translate(-0.6em,0.4em);
}

.Ham-change .Header-ham-bar2 {opacity: 0;}

.Ham-change .Header-ham-bar3 {
    -webkit-transform: rotate(45deg) translate(-0.5em, -0.5em);
    transform: rotate(45deg) translate(-0.5em, -0.5em);
}

/* Ham End */

/* Mobile Menu */

.Header-mobile-menu-container {
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.4s ease;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    max-height: calc(100vh - 60px);
    overflow: scroll;
}

.Header-mobile-menu-separator {
    width: 90%;
    margin-top: 1em;
    margin-bottom: 1em;
    border-bottom: 1px solid;
}

.Header-mobile-animate {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

/* Mobile Menu End */
.Footer {
    width: 100%;
