}

.Favorite-container {
    width: 100%;
    height: 100vh;
    position: relative;
}

.Favorite-carousel {
    top: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    transition: opacity 0.5s ease-in-out;
}

.Favorite-spinner {
    width: 3em;
}

.Favorite-icon {
    position: relative;
    -webkit-animation-name: iconAnimation; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 3s; /* Safari 4.0 - 8.0 */
    -webkit-animation-iteration-count: infinite; /* Safari 4.0 - 8.0 */
    animation-name: iconAnimation;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    animation-iteration-count: infinite;
}

@keyframes iconAnimation {
    0% {opacity: 0;top: -0.4em}
    50% {opacity: 1;}
    100% {opacity: 0;top: 0.4em}
}

@-webkit-keyframes iconAnimation {
    0% {opacity: 0;top: -0.4em}
    50% {opacity: 1;}
    100% {opacity: 0;top: 0.4em}
}

@-webkit-keyframes slideShowFadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

@keyframes slideShowFadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

@-webkit-keyframes slideShowFadeOut {
    0% {opacity: 1;}
    100% {opacity: 0;
    display: none}
}

@keyframes slideShowFadeOut {
    0% {opacity: 1;}
    100% {opacity: 0;
    display: none}
}

