.Blink-spinner-container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
    box-sizing: border-box;
}

.Blink-spinner-sub-container {
    position: relative;
    width: 100%;
